<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/coupon/list" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>可进行添加平台优惠券</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="优惠券名称" prop="coupon.name" :error='errorMessage["coupon_name"]'>
          <el-input v-model="formData.coupon.name"></el-input>
        </el-form-item>
        <el-form-item label="优惠券类型" prop="coupon.couponType" :error='errorMessage["coupon_couponType"]'>
          <el-radio-group v-model="formData.coupon.couponType" @change="changeType">
            <el-radio :label="1">活动优惠券</el-radio>
            <el-radio :label="2">普通优惠券</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="formData.coupon.couponType==1" prop="coupon.useEndTime">
          <cms-picker disabledType='todayAfter' v-model="timer" style="width:380px" @change="getTimer"></cms-picker>
        </el-form-item>
        <el-form-item label="有效期" prop="coupon.useDay" v-if="formData.coupon.couponType==2">
          <el-input v-model="formData.coupon.useDay">
            <template slot="prepend">固定天数</template>
            <template slot="append">天</template>
          </el-input>
        </el-form-item>
        <el-form-item label="使用条件" prop="coupon.conOrderAmount" :error='errorMessage["coupon_conOrderAmount"]'>
          <el-input v-model="formData.coupon.conOrderAmount" maxlength="8">
            <template slot="prepend">满</template>
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <el-form-item label="面值" prop="coupon.discountAmount" :error='errorMessage["coupon_discountAmount"]'>
          <el-input v-model="formData.coupon.discountAmount">
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <el-form-item label="发放限制" prop="coupon.issueNum">
          <el-input v-model="formData.coupon.issueNum" maxlength="8"></el-input>
          <div class="gray">输入几表示限制几张，输入0表示不限制</div>
        </el-form-item>
        <el-form-item label="领取限制" prop="coupon.getNum" :error='errorMessage["coupon_getNum"]'>
          <el-input v-model="formData.coupon.getNum">
            <template slot="prepend">每用户</template>
            <template slot="append">张</template>
          </el-input>
        </el-form-item>
        <el-form-item label="优惠券使用范围" prop="coupon.couponScope" :error='errorMessage["coupon_couponScope"]'>
          <el-radio-group v-model="formData.coupon.couponScope">
            <el-radio :label="1">全部商品</el-radio>
            <el-radio :label="2">自营商品</el-radio>
            <el-radio :label="3">部分类目</el-radio>
          </el-radio-group>
          <el-form-item  label="选择类目" label-width='80px' v-if="formData.coupon.couponScope===3">
          <category-cascader style="width:298px;" v-model="formData.ctgIds"></category-cascader>
          </el-form-item>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button @click="save(true)" v-color>保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { couponSave } from '@/api/marketing/index.js'
import categoryCascader from '@/components/cms/categoryCascader'

var defaultData = {
  coupon: {
    conOrderAmount: '',
    discountAmount: '',
    getMode: 1,
    getNum: '',
    expirateType: 2,
    issueNum: '',
    name: '',
    useBeginTime: '',
    useEndTime: '',
    useDay: '',
    couponType: 2,
    couponScope: 1
  },
  issueType: 1,
  ctgIds: []
}

export default {
  components: { categoryCascader },
  mixins: [form],
  data() {
    var isAmount = (rule, value, callback) => {
      if (Number(value) < Number(this.formData.coupon.conOrderAmount)) {
        callback()
      } else {
        callback(new Error('面值不能大于使用条件'))
      }
    }
    var notZero = (rule, value, callback) => { // 验证不能输0
      if (value === '0') {
        callback(new Error('请输入大于0的正整数'))
      } else {
        callback()
      }
    }
    return {
      timer: [],
      formData: Object.assign({}, defaultData),
      rules: {
        'coupon.name': [rules.required('请填写优惠券名称'), rules.len(1, 20)],
        'coupon.couponType': [rules.required()],
        'coupon.useDay': [rules.required(), rules.number('请输入大于0的正整数'), rules.len(1, 3), rules.custom(notZero)],
        'coupon.conOrderAmount': [rules.required(), rules.number('请输入大于0的正整数'), rules.len(1, 8), rules.custom(notZero)],
        'coupon.issueNum': [rules.required(), rules.number('请输入大于等于0的正整数'), rules.len(1, 8)],
        'coupon.getNum': [rules.required(), rules.number('请输入大于0的正整数'), rules.len(1, 8), rules.custom(notZero)],
        'coupon.discountAmount': [
          rules.required('此项必填'),
          rules.number('请输入大于0的正整数'),
          rules.custom(isAmount)
        ],
        issueType: [rules.required()],
        'coupon.useEndTime': [rules.required()]
      }
    }
  },
  methods: {
    changeType(value) {
      this.formData.coupon.expirateType = value
    },
    getTimer(value) {
      this.formData.coupon.useBeginTime = value[0]
      this.formData.coupon.useEndTime = value[1]
    },
    save(state) {
      this.saveForm(couponSave, this.formData, state, '/coupon/list', 'list').then(
        res => {}
      )
    },
    rest() {
      this.autoFocus()
      this.$refs.form.resetFields()
      this.destroyed()
      this.timer = []
    }
  },
  created() {}
}
</script>

<style>
</style>



